const api = require('../../config/api.js');
const util = require('../../utils/util.js')
const user = require("../../services/user.js")
const app = getApp()	
export default {
	data() {
		return {
			orderInfo: {},
			orderGoods: []
		}
	},
	//初始化
	onLoad() {
		let pages = getCurrentPages();
		let { options } = pages.pop();
		if(options.id) {
			this.getInfo(options.id);
		}
	},
	// 页面显示
	onShow() {
		
	},
	//分享
	onShareAppMessage() {
		wx.showShareMenu({
			withShareTicket:true
		})
		let code = wx.getStorageSync('code');
		this.setCode(code)
		return {
			title: '@你：这里每天更新好多免费的抽奖，快来抽奖吧~',
			path: '/pages/user/index?code='+code,
			imageUrl: '../../static/images/shareImage.jpg'
		}
	},
	//下拉刷新
	onPullDownRefresh(e){
		setTimeout(function () {
			wx.stopPullDownRefresh();
		}, 1000);
	},
	methods: {
		//获取订单详情
		getInfo(id) {
			util.request(api.getOrderInfo,{order_id: id},'post').then(res=>{
				if(res.code == 0) {
					this.orderInfo = res.data.order;
					this.orderGoods = res.data.goods;
				}
			})
		},
		dealPrcture(str) {
			if(!str) return ;
			let newStr = str.split(',');
			return newStr[0]
		},
		//取消订单
		cancelOrder() {
			
		},
		//去付款
		payOrders() {
			
		},
		//传递分享code进去
		setCode(code) {
			util.request(api.setShareInfo,{code: code},'post').then(res=>{})
		}
	}
}
